// }
.btn-types {
  display: inline-block;
  padding: 8px 10px;
  max-width: 150px;
  margin-left: 10px;
}

@media (max-width: 650px) {
  .types__card-list {
    grid-template-columns: repeat(1, 1fr);
  }
}
