.question-form {
    margin-bottom: $sec-bottom;
    //
    &-container {
        display: flex;
        flex-direction: column;
        align-items: center;
        background-color: $question-form-background;
        border-radius: $radius;
        padding: 30px 0;
    }
    &__title-wrapper {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    &__title {
        font-size: 26px;
        max-width: 1025px;
        text-align: center;
        margin-bottom: 10px;
    }
    &__number-phone {
        font-size: 26px;
        font-weight: 600;
        color: $blue;
        margin-bottom: 30px;
    }
    &__descr {
        font-size: 18px;
        font-weight: 500;
        margin-bottom: 30px;
        max-width: 625px;
        text-align: center;
    }
    &__line-title {
        margin-bottom: 20px;
    }
    // form
    &__container-form {
        max-width: 440px;
    }
    &__form-data-container {
        display: flex;
        justify-content: space-between;
        width: 100%;
    }
    &__wrapper-id-client {
        width: 100%;
    }
    &__client-id-container {
        display: flex;
        justify-content: space-between;
        margin-bottom: 20px;
    }
    &__phone-input {
        padding-left: 10px;
        border-radius: 50px;
        border: 1px solid $gray;
        height: 41px;
        width: 100%;
    }
    // &__phone-input::placeholder {
    //     padding-left: 6px;
    // }
    &__city-select {
        width: 100%;
    }
    &__checkbox-container {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-bottom: 30px;
        text-align: center;
    }
    // checkbox
    &__checkbox-label {
        position: relative;
        font-size: 13px;
    }
    // &__checkbox {
    //     position: absolute;
    //     visibility: hidden;
    // }
    &__link {
        color: $blue;
    }
    // btn
    &__btn-submit {
        width: 100%;
    }
    &__text-require {
        font-size: 10px;
        max-width: 360px;
        margin-top: 5px;
    }
}

input[data-validate-rules^="phone"]{
    padding-left: 10px;
}

.js-validate-error-label {
    margin-top: 10px;
    font-size: 12px;
    color: $red;
    max-width: 300px;
}

.form-label {
    display: block;
    margin-bottom: 5px;
    font-size: .75rem;
    font-weight: 300;
    padding-left: 0.8rem;
}

.choices {
    width: 100%;
}

.choices__inner {
    border: 1px solid $gray;
    border-radius: 50px;
    min-height: auto;
    transition: box-shadow .1s ease-in-out;
}

.is-open .choices__inner {
    border-radius: $radius;
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.295);
}

.is-focused .choices__inner {
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.25);
}

.choices__list--dropdown {
    border-radius: $radius;
    border: 1px solid $blue;
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.25);
}

.choices__list--dropdown .is-active {
    border-radius: $radius;
    border: 1px solid $blue;
}

.choices__heading {
    color: $blue;
    font-weight: 600;
    font-size: 16px;
}

.choices__list--dropdown .choices__item {
    font-size: 12px;
}

.choices__item--selectable::after {
    color: $blue;
    background-color: #fff;
    padding: 2px 4px;
    border-radius: 4px;
}

.choices__list--dropdown .choices__item--selectable.is-highlighted:after {
    opacity: 1
}

#choices--selectModel-item-choice-1 {
    display: none;
}

.choices__list--multiple .choices__item {
    background-color: $blue;
    border: 1px solid $blue;
}

// pop up calculate
.calc-pop-up {
    position: relative;
    opacity: 0;
    pointer-events: none;
    display: flex;
    justify-content: center;
    position: absolute;
    margin: 0 auto;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(105, 105, 105, 0.5);
    transition: display .3s ease-in-out;
    z-index: 199;
}

.calculateApplication-popUp__container {
    display: none;
    padding: 0 15px;
    width: 55%;
    height: 80%;
    overflow: auto;
    margin: auto;
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: 999;
    background-color: #fff;
    // height: 500px;
    border-radius: $radius;
    z-index: 220;
}

.popUp__container-is-active {
    display: block;
}

.calculateApplication-popUp__content-wrapper {
    position: relative;
    display: flex;
}

.calcapp-popup__close {
    position: absolute;
    top: 10px;
    right: 10px;
    padding: 16px;
    background-color: #fff;
    border: 1px solid $blue;
    border-radius: $btn-radius;
    background-image: url(../img/close.svg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: 10px;
}

